import RoleTypeService from '@/services/commons/RoleTypeService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let RoleTypeTableMixin = {
  mixins: [LoadingMixin, DisabledClassMixin, ListLayoutMixin],
  props: {
    defaultRoleTypeTableFilter: {
      type: String,
      default: null
    },
    defaultRoleTypeTableOrderBy: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      roleTypeTableData: [],
      roleTypeTableFilterTimer: null,
      roleTypeTableFilter: this.defaultRoleTypeTableFilter,
      roleTypeTableOrderBy: this.defaultRoleTypeTableOrderBy,
      roleTypeTableContainer: 'loading-container',
      roleTypeTableBusy: false,
      roleTypeTableTotalRows: 0,
      roleTypeTableCurrentPage: 1,
      roleTypeTablePerPage: 10
    }
  },
  computed: {
    roleTypeTableNoDataText: function () {
      return this.roleTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    roleTypeTableTotalPages: function () {
      return Math.ceil(this.roleTypeTableTotalRows / this.roleTypeTablePerPage)
    },
    roleTypeTablePagingInfo: function () {
      return this.pagingInfo(this.roleTypeTableCurrentPage, this.roleTypeTablePerPage, this.roleTypeTableTotalRows)
    }
  },
  methods: {
    loadRoleTypeTableData (loadingContainerId) {
      let self = this
      this.roleTypeTableBusy = true
      this.roleTypeTableContainer = loadingContainerId || this.roleTypeTableContainer
      this.startLoading(this.roleTypeTableContainer)
      RoleTypeService.roleTypes({
        pageIndex: this.roleTypeTableCurrentPage - 1,
        pageSize: this.roleTypeTablePerPage,
        filter: this.roleTypeTableFilter,
        orderByField: this.roleTypeTableOrderBy,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.roleTypeTableContainer)
        self.roleTypeTableBusy = false
        self.roleTypeTableTotalRows = response.data.totalRows
        self.roleTypeTableData = response.data.typeList || []
      }).catch(function (error) {
        self.stopLoading(self.roleTypeTableContainer)
        self.roleTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshRoleTypeTable () {
      this.loadRoleTypeTableData()
    },
    filterRoleTypeTable (filter) {
      let self = this
      if (this.roleTypeTableFilter !== filter) {
        clearTimeout(this.roleTypeTableFilterTimer)
        this.roleTypeTableFilterTimer = setTimeout(() => {
          self.roleTypeTableFilter = filter
          self.loadRoleTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.roleTypeTableFilterTimer)
      }
    },
    orderRoleTypeTable (orderBy, active) {
      this.roleTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadRoleTypeTableData()
    }
  },
  watch: {
    roleTypeTableCurrentPage: function () {
      this.refreshRoleTypeTable()
    },
    roleTypeTablePerPage: function () {
      this.refreshRoleTypeTable()
    }
  }
}

export default RoleTypeTableMixin
