import Endpoint from '@/services/Endpoint'

export default {
  roleTypes (payload) {
    return Endpoint().get('/s1/commons/party/roleType', {
      params: payload
    })
  },
  createRoleType (payload) {
    return Endpoint().post('/s1/commons/party/roleType', payload)
  },
  modifyRoleType (payload) {
    return Endpoint().patch('/s1/commons/party/roleType', payload)
  }
}
